@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&family=Kufam:wght@500&family=Orbitron:wght@500&display=swap);
.Cv {
  background-image: url(/cv/static/media/back.3e158932.png);
  color: white;
  overflow: hidden;
}

.body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.Header {
  display: flex;
  align-items: center;
  background-color: black;
  height: 60px;
  width: 100%;
  animation: move1 6s infinite;
}

@keyframes move1 {
  50% {
    box-shadow: 0px 0px 10px #29f3c3;
  }
}

.title {
  display: flex;
  flex-direction: column-reverse;
  height: 5vh;
  width: 100%;
  font-size: 1em;
  font-family: "Orbitron", sans-serif;
  box-shadow: 5px 5px 5px #29f3c3;
  animation: defilement-rtl 3s linear;
}

@keyframes defilement-rtl {
  0% {
    transform: translate3d(250%, 0, 0);
  }
  100% {
    transform: translate3d(0%, 0, 0);
  }
}

@media screen and (min-width: 768px) {
  .Header {
    height: 80px;
  }

  .title {
    font-size: 1.4em;
  }
}

@media screen and (min-width: 1380px) {
  .title {
    font-size: 2em;
  }
}

.column_1 {
  margin: 15px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 30%;
}

.moi {
  position: relative;
  z-index: 1000;
  width: 30vw;
  border: 2px solid #29f3c3;
  box-shadow: 0px 0px 10px 2px #29f3c3;
  animation: defilement-rtl9 2s linear;
  transition: 0.5s ease;
}

.moi:hover {
  transform: scale(1.09);
}

@keyframes defilement-rtl9 {
  0% {
    transform: translateY(-200%);
  }
  100% {
    transform: translateY(0%);
  }
}

.job {
  font-size: 0.7em;
  margin-left: 5px;
  margin-top: 10px;
  text-shadow: 55px 55px 55px 55px #29f3c3;
}

.animation_h2 {
  animation: defilement-rtl4 4s linear;
}

@keyframes defilement-rtl4 {
  0% {
    transform: translate3d(-200%, 0, 0);
  }
  100% {
    transform: translate3d(0%, 0, 0);
  }
}

.h_line2 {
  width: 90%;
  height: 1px;
  background-color: #29f3c3;
  box-shadow: 0px 0px 15px 3px #29f3c3;
  animation: defilement-rtl7 6s linear;
}

.h_line2_experience {
  margin-left: -100%;
  width: 500%;
  height: 1px;
  background-color: #29f3c3;
  box-shadow: 0px 0px 8px 2px #29f3c3;
  animation: defilement-rtl11 6s linear;
}

@keyframes defilement-rtl11 {
  0% {
    transform: translate3d(250%, 0, 0);
  }
  100% {
    transform: translate3d(0%, 0, 0);
  }
}

@keyframes defilement-rtl7 {
  0% {
    transform: translate3d(-300%, 0, 0);
  }
  100% {
    transform: translate3d(0%, 0, 0);
  }
}

.tools {
  padding-top: 15px;
}

.hobbies {
  padding-top: 20px;
  text-decoration: none;
}

.contact {
  padding-top: 20px;
}

.spec,
.spec_contact {
  animation: defilement-rtl6 4.5s linear;
}

@keyframes defilement-rtl6 {
  0% {
    transform: translate3d(-200%, 0, 0);
  }
  100% {
    transform: translate3d(0%, 0, 0);
  }
}

.spec {
  display: flex;
  align-items: center;
  text-indent: 10px;
  padding: 12px;
  width: 110%;
  transition: 0.5s ease;
}

.spec_contact {
  display: flex;
  align-items: center;
  width: 110%;
  padding-top: 20px;
  transition: 0.5s ease;
}

.spec_contact:hover {
  transform: scale(1.09);
}

.spec:hover {
